import React from 'react';
import { Container, Box, Typography } from '@mui/material';

const About = () => {
    return (
        <Container maxWidth="md" sx={{ mt: 4 }}>
            <Box sx={{ textAlign: 'center', p: 2 }}>
                <Typography variant="h4" gutterBottom>
                    Hakknda
                </Typography>
                <Typography variant="body1" paragraph>
                    Bu uygulama, kullanclarn eitimler hakknda bilgi edinmesi, eitmenlerle toplant planlamas ve birebir grmeler yapmas iin tasarlanmtr.
                </Typography>
                <Typography variant="body1" paragraph>
                    Proje, modern bir arayz ve kullanc dostu deneyim sunarak gelecein eitim ve iletiim ihtiyalarna yant vermeyi amalar.
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Gelitirici: Yavuz Asmal
                </Typography>
            </Box>
        </Container>
    );
};

export default About;
